import React, { useState, Suspense, lazy } from "react";
import { useNavigate } from "react-router-dom";
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";

const FormForAuth = lazy(() => import("../../components/FormForAuth/FormForAuth.jsx"));

function Auth() {
  const [userData, setUserData] = useState({ email: "", password: "", name: "" });
  const navigate = useNavigate();
  const [error, setError] = useState("");

  async function handleLogin(e) {
    e.preventDefault();
    const auth = getAuth();

    try {
      await signInWithEmailAndPassword(auth, userData.email, userData.password);
      navigate("/");
      setUserData({ email: "", password: "", name: "" });
    } catch (error) {
      console.log(error.message);
      error.message && setError(error.message || "Введённые данные не верные");
    }
  }
  console.log(error);
  return (
    <Suspense fallback={<div>Loading...</div>}>
      <FormForAuth error={error} handleLogin={handleLogin} userData={userData} setUserData={setUserData} />
    </Suspense>
  );
}
export default Auth;
